#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f8ff; }

.padded {
  padding: 1%; }

.half, .sixty, .forty {
  overflow: hidden; }

.sixty .content {
  margin-right: 1%;
  margin-bottom: 1%;
  padding: 0 1% 1.4% 1%;
  border: 1px solid black;
  overflow: none; }

.left {
  float: left; }

/* IE doesn't support the @media tag, so define basic size rules here
first before separating out by screen size for responsive design */
.half {
  width: 50%; }

.sixty {
  width: 60%; }

.forty {
  width: 40%; }

.third {
  width: 33%; }

.fifteen {
  width: 15%; }

.eighty-five {
  width: 85%; }

.seventy-five {
  width: 75%; }

.twenty-five {
  width: 25%; }

.rcol {
  width: 16%; }

.lcol {
  width: 80.68%; }

.mobile {
  display: none; }

.noTablet {
  display: none; }

/* print styles */
/** RESPONSIVE DESIGN RULES */
@media screen and (min-width: 320px) {
  .noMobile, .noMobileCol {
    display: none; }
  .mobile {
    display: inherit; }
    .mobile .noTablet {
      display: inherit; }
  .mobileCol {
    display: table-cell; }
  .right {
    float: left; }
  .half {
    width: 100%; }
  .sixty {
    width: 100%; }
  .forty {
    width: 100%; }
    .forty .half {
      width: 50%; }
  .third {
    width: 100%; }
  .seventy-five {
    width: 100%; }
  .twenty-five {
    width: 0%;
    display: none; }
  .lcol {
    width: 100%; }
  .rcol {
    width: 0%;
    display: none; } }

@media screen and (min-width: 600px) {
  div.animal div.left_half {
    width: 28%; }
    div.animal div.left_half div.thumbnail {
      width: 100%; }
  div.animal div.right_half {
    width: 72%;
    float: right; }
  .seventy-five {
    width: 75%; }
  .twenty-five {
    width: 25%;
    display: inherit; }
  #footer {
    height: 32px; } }

@media screen and (min-width: 768px) {
  .tablet {
    display: inherit; }
  .tabletCol {
    display: table-cell; }
  .noTablet {
    display: none; }
  .half {
    width: 50%; }
  .right {
    float: right; }
  .third {
    width: 50%; }
  .sixty {
    width: 60%; }
  .forty {
    width: 40%;
    display: inherit; } }

@media screen and (min-width: 900px) {
  .rcol {
    width: 16%;
    display: inherit; }
  .lcol {
    width: 80.68%; } }

@media screen and (min-width: 1024px) {
  .noMobile {
    display: inherit; }
  .noMobileCol {
    display: table-cell; }
  .mobile {
    display: none; }
  .third {
    width: 33%; } }

