/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

img {
  display: block; }

a {
  color: #fd8d5c;
  font-weight: 700; }

.btn {
  display: inline-block;
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder;
  background-color: #fd8d5c;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.2rem 3rem;
  border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

button.btn {
  border: none; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.privacy-policy .photo-text-gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: stretch; }

.privacy-policy .animal-card {
  margin: 4rem 0;
  width: 34rem;
  border: 1px solid #b1b1b1;
  border-radius: 0.5rem;
  padding: 1.5rem 1rem 1rem; }
  .privacy-policy .animal-card .photo {
    align-self: center; }
  .privacy-policy .animal-card img {
    max-height: 31rem;
    max-width: 100%; }
  .privacy-policy .animal-card .pet-card {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 100%; }
  .privacy-policy .animal-card h3 {
    color: #a0cd3a;
    font-size: 3.0rem;
    margin: 1rem 0 0.3rem; }
  .privacy-policy .animal-card ul {
    list-style: none;
    font-weight: 500; }
  .privacy-policy .animal-card label {
    display: none; }
  .privacy-policy .animal-card .description {
    display: flex;
    flex-direction: column;
    flex-grow: 1; }
  .privacy-policy .animal-card .btn {
    align-self: center; }
