/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

img {
  display: block; }

a {
  color: #fd8d5c;
  font-weight: 700; }

.btn {
  display: inline-block;
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder;
  background-color: #fd8d5c;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.2rem 3rem;
  border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

button.btn {
  border: none; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.footer {
  width: 100%;
  height: 54rem;
  background-image: url("../assets/images/footer_bg.png");
  padding: 5rem 8rem;
  position: relative; }
  .footer .main {
    display: flex;
    justify-content: space-between;
    color: white; }
    .footer .main h1 {
      font-size: 3.5rem;
      font-family: 'Raleway', sans-serif; }
    .footer .main hr {
      background-color: #a0cd3a;
      margin: .5rem 0 2rem;
      width: 4rem;
      height: 0.3rem;
      border: 0; }
    .footer .main p {
      font-size: 1.8rem; }
    .footer .main input {
      border: 0; }
      .footer .main input[type='text'] {
        background-color: rgba(255, 255, 255, 0.5);
        margin: 2rem 0;
        padding: 1rem;
        width: 30rem;
        color: #fff;
        font-size: 1.8rem; }
        .footer .main input[type='text']::placeholder {
          color: #a0cd3a; }
    .footer .main .image-list {
      display: flex;
      align-items: center; }
      .footer .main .image-list img {
        margin-right: 0.75rem; }
  .footer .bottom {
    position: absolute;
    bottom: 0;
    padding-right: 8rem; }
    .footer .bottom p {
      color: #fff;
      margin-bottom: 3rem; }
      .footer .bottom p a {
        color: #fff;
        font-weight: 500; }
