/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

img {
  display: block; }

a {
  color: #fd8d5c;
  font-weight: 700; }

.btn {
  display: inline-block;
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder;
  background-color: #fd8d5c;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.2rem 3rem;
  border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

button.btn {
  border: none; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.form {
  background: #1c75bb;
  padding: 1rem 1rem 2.2rem;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .form ul {
    list-style: none;
    width: 100%;
    margin-bottom: 1.8rem; }
  .form li {
    display: flex;
    justify-content: flex-end;
    align-items: center; }
    .form li > label {
      flex: 1;
      text-align: right;
      margin: 0.5rem; }
    .form li > input, .form li .input, .form li .input-select, .form li textarea {
      flex: 2;
      margin: 0.5rem;
      padding: 0.5rem; }
    .form li > input, .form li .input-select {
      background-color: #fff; }
    .form li > .input > input {
      vertical-align: middle; }
  .form label {
    font-size: 2rem; }
  .form .input label {
    margin-right: 2.5rem; }
  .form .input input[type='radio'] {
    margin-right: 0.5rem; }
  .form .input input[type='checkbox'] {
    margin-right: 0.7rem; }
  .form textarea {
    height: 10rem; }
  .form h2, .form label, .form p {
    color: #fff; }
  .form h2 {
    font-size: 3.5rem; }
  .form h2, .form p {
    align-self: flex-start; }
