/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

img {
  display: block; }

a {
  color: #fd8d5c;
  font-weight: 700; }

.btn {
  display: inline-block;
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder;
  background-color: #fd8d5c;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.2rem 3rem;
  border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

button.btn {
  border: none; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.volunteer-form textarea {
  width: 100%;
  margin-bottom: 3rem; }

.volunteer-form .thanks {
  align-self: auto; }
