/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

img {
  display: block; }

a {
  color: #fd8d5c;
  font-weight: 700; }

.btn {
  display: inline-block;
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder;
  background-color: #fd8d5c;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.2rem 3rem;
  border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

button.btn {
  border: none; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.header {
  width: 100%;
  height: 15rem;
  padding: 4rem 1rem 0 3rem;
  background-color: #1c75bb;
  position: relative; }
  .header.home {
    background-color: #fff; }
  .header__logo {
    display: inline-block;
    padding-right: 1rem; }
    .header__logo img {
      height: 8.2rem; }
  .header__nav {
    display: inline-block;
    list-style: none; }
    .header__nav.home li a {
      color: #1c75bb; }
    .header__nav li {
      display: inline-block;
      margin-left: 4rem; }
      .header__nav li a, .header__nav li .popup-link {
        background: none;
        border: none;
        color: #fff;
        text-decoration: none;
        font-size: 1.7rem;
        font-weight: 700;
        border-bottom: 0.2rem solid transparent;
        transition: border-bottom 0.2s;
        padding-bottom: 0.3rem;
        white-space: nowrap; }
        .header__nav li a:hover, .header__nav li a:active, .header__nav li .popup-link:hover, .header__nav li .popup-link:active {
          border-bottom: 0.2rem solid #a0cd3a;
          padding-bottom: 0.3rem; }
    .header__nav ul li, .header__nav.home ul li {
      margin: 0 0 0.9rem 0; }
      .header__nav ul li:last-child, .header__nav.home ul li:last-child {
        margin-bottom: 0; }
      .header__nav ul li a, .header__nav.home ul li a {
        color: #000;
        font-weight: 500; }
  .header .donate-cat {
    bottom: 1rem;
    right: 4rem;
    position: absolute; }
    @media (max-width: 1200px) {
      .header .donate-cat {
        display: none; } }
    .header .donate-cat a {
      z-index: 200;
      position: relative; }
    .header .donate-cat img {
      position: absolute;
      top: -71px;
      left: 52px;
      z-index: 100;
      max-height: 237px; }
