/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

img {
  display: block; }

a {
  color: #fd8d5c;
  font-weight: 700; }

.btn {
  display: inline-block;
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder;
  background-color: #fd8d5c;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.2rem 3rem;
  border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

button.btn {
  border: none; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.sponsors {
  width: 100%;
  background-color: #fff;
  display: flex;
  justify-content: flex-start;
  padding-left: 8rem;
  padding-right: 8rem; }
  .sponsors > div {
    padding-top: 8rem;
    padding-bottom: 8rem; }
    .sponsors > div.scroller-tiles {
      padding: 0;
      width: 60%;
      margin: 0 6rem 0; }
      .sponsors > div.scroller-tiles > div {
        width: 18rem;
        height: 23.7rem;
        font-size: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: center; }
        .sponsors > div.scroller-tiles > div img {
          width: 100%; }
  .sponsors h1, .sponsors .scroller-arrow button {
    font-size: 3.5rem;
    color: #1c75bb;
    margin-right: 6rem;
    white-space: nowrap; }
  .sponsors hr {
    background-color: #fd8d5c; }
  .sponsors .scroller-arrow button {
    margin-right: 0;
    color: #000;
    font-weight: bolder; }
