/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

img {
  display: block; }

a {
  color: #fd8d5c;
  font-weight: 700; }

.btn {
  display: inline-block;
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder;
  background-color: #fd8d5c;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.2rem 3rem;
  border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

button.btn {
  border: none; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.animal-details > div {
  display: flex; }
  .animal-details > div .details {
    padding-left: 4rem; }
    .animal-details > div .details p {
      font-size: 1.7rem; }
    .animal-details > div .details label {
      font-weight: 700;
      margin-right: 0.5rem; }
    .animal-details > div .details .good-with {
      font-size: 1.2rem; }
      .animal-details > div .details .good-with label {
        margin-left: 0.6rem; }
        .animal-details > div .details .good-with label:first-child {
          margin-left: 0; }
      .animal-details > div .details .good-with span {
        margin-right: 0.6rem; }
    .animal-details > div .details .video {
      display: flex;
      align-items: center; }
      .animal-details > div .details .video img {
        height: 3rem;
        margin-left: 0.5rem; }
  .animal-details > div .simple-details ul {
    list-style: none;
    margin-top: 1rem; }
  .animal-details > div .simple-details li {
    display: flex; }
    .animal-details > div .simple-details li > label {
      flex: 1;
      text-align: right;
      margin: 0.1rem;
      padding: 0.1rem; }
    .animal-details > div .simple-details li > span {
      flex: 2;
      margin: 0.1rem;
      padding: 0.1rem;
      font-weight: 700; }
  .animal-details > div button.popup {
    border: none; }
  .animal-details > div .gallery {
    display: flex;
    justify-content: flex-start;
    align-items: center; }
    .animal-details > div .gallery .popup-content img {
      max-height: 100vh;
      margin: 0 auto; }
    .animal-details > div .gallery .scroller-tiles > div, .animal-details > div .gallery .scroller-arrow {
      display: flex;
      justify-content: center;
      align-items: center; }
    .animal-details > div .gallery img {
      max-height: 100%;
      max-width: 100%; }
    .animal-details > div .gallery button {
      cursor: pointer; }
    .animal-details > div .gallery--popup {
      height: 80rem;
      width: 1200px; }
    .animal-details > div .gallery--big {
      position: relative;
      height: 40rem;
      width: 42rem; }
      .animal-details > div .gallery--big .scroller-tiles, .animal-details > div .gallery--big .scroller-tiles > div {
        width: 100%;
        height: 100%; }
      .animal-details > div .gallery--big .scroller-arrow {
        position: absolute;
        z-index: 100;
        top: 50%;
        transform: translateY(-50%);
        background-color: #fff;
        opacity: 0.4;
        text-align: center; }
        .animal-details > div .gallery--big .scroller-arrow-right {
          right: 0; }
        .animal-details > div .gallery--big .scroller-arrow button {
          font-size: 2rem;
          width: 2rem;
          height: 6rem; }
    .animal-details > div .gallery--thumbnail {
      height: 23rem; }
      .animal-details > div .gallery--thumbnail .scroller-arrow button {
        font-size: 2rem;
        color: #1c75bb; }
      .animal-details > div .gallery--thumbnail .scroller-tiles {
        padding: 0;
        width: 38rem;
        height: 100%;
        margin: 0 1rem 0; }
        .animal-details > div .gallery--thumbnail .scroller-tiles > div {
          width: 8rem;
          height: 100%;
          font-size: 1rem; }
